import Vue from 'vue'
import Router from 'vue-router'
import TestFinance from '../pages/test_finance/TestFinance'
import OtherRouter from '../pages/other_router/OtherRouter'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'TestFinance',
      component : TestFinance
    },
    {
      path: '/run',
      name: 'OtherRouter',
      component: OtherRouter
    }
  ]
})



